<template>

	<view class="container">
		<!-- <view class="loading-overlay" v-if="isLoading">
		  <view class="loading-content">
		    <uni-icons type="spinner-cycle" size="30" color="#1ec57c"></uni-icons>
		    <text class="loading-text">加载中...</text>
		  </view>
		</view> -->
		<!-- 主机状态区域 -->
		<view class="host-status">
			<view class="host-status-dot" :class="{ online: isHostOnline }"></view>
			<text class="host-status-text">{{ isHostOnline ? '主机在线' : '主机离线' }}</text>
			<text v-if="isLoading" class="loading-dots">&nbsp;</text>
			<view v-if="!isLoading" class="refresh-icon" @click="refreshStatus">
				<uni-icons type="refresh" size="16" color="#666666"></uni-icons>
			</view>

		</view>
		<!-- 登录状态区域 -->
		<view class="login-section">
			<view class="login-card tencent">
				<view class="login-title">腾讯元宝/福帮手</view>
				<view v-if="!isTencentLoggedIn" class="login-btn" @click="showTencentLogin">
					<uni-icons type="person" :size="16" color="#666"></uni-icons>
					<text>点击登录</text>
				</view>
				<view v-else class="logged-status">
					<text class="username">{{ tencentUsername }}</text>
					<text class="status-text">{{YBname}}</text>
				</view>
			</view>
			
			<view class="login-card mita">
				<view class="login-title">腾讯文档</view>
				<view v-if="!isMitaLoggedIn" class="login-btn" @click="showMitaLogin">
					<uni-icons type="person" size="16" color="#666"></uni-icons>
					<text>点击授权</text>
				</view>
				<!-- <view v-else class="logged-status">
					<text class="username">{{ mitaUsername }}</text>
					<text class="status-text">{{MTname}}</text>
				</view> -->
			</view>
			

		</view>
		<!-- AI配置区域 -->
	
		<!-- 输入执行区域 -->
		<view class="input-section">
			<textarea class="input-area" v-model="userInfoReq.userPrompt" placeholder="请输入AI指令..." :maxlength="-1" />
			<button class="execute-btn" @click="executeTask" :disabled="!canExecute">
				开始执行
			</button>
			<view class="progress-wrapper" v-if="isExecuting">

				<progress :percent="taskProgress" activeColor="#1ec57c" backgroundColor="#E5E7EB" stroke-width="3" />
				<text class="progress-text">执行进度：{{taskProgress}}%</text>
	<!-- 
					<view class="stream-output">{{ streamOutput }}</view> -->

				
			</view>
		</view>
		<!-- 结果预览区域 -->
		<view class="result-section" v-if="resultContent">


			<view class="result-tabs">
				<view v-for="tab in aiResults" :key="tab.type"
					:class="['tab-item', { active: currentTab === tab.type }]" @click="switchTab(tab.type)">
					{{ tab.label }}
				</view>
			</view>
			<view class="result-header">
				<!-- <view class="result-title">结果预览</view> -->
				<view class="copy-btn" @click="copyResult">
					<uni-icons type="copy" size="16" color="#666666"></uni-icons>
					<text>一键复制</text>
				</view>
			</view>

			<scroll-view class="scroll-container" scroll-y>
				<view v-html="resultContent" class="content"></view>
				<!-- 	<text class="long-text">{{ resultContent }}</text> -->
			</scroll-view>

		</view>
		<!-- 腾讯登录弹窗 -->
		<uni-popup ref="tencentPopup" type="center">
			<view class="login-popup">
				<view class="popup-title">腾讯元宝登录</view>
				<view class="input-group">
					<input class="input-field" v-model="tencentForm.account" placeholder="请输入账号" />
					<view class="verify-group">
						<input class="input-field verify-input" v-model="tencentForm.code" placeholder="请输入验证码" />
						<button class="verify-btn" @click="sendVerifyCode" :disabled="isCountingDown">
							{{ countDownText }}
						</button>
					</view>
				</view>
				<view class="popup-btns">
					<button class="cancel-btn" @click="closeTencentPopup">取消</button>
					<button class="confirm-btn" @click="confirmTencentLogin">确认</button>
				</view>
			</view>
		</uni-popup>
		<!-- 秘塔登录弹窗 -->
		<uni-popup ref="mitaPopup" type="center">
			<view class="login-popup">
				<view class="popup-title">秘塔AI登录</view>
				<view class="input-group">
					<input class="input-field" v-model="mitaForm.account" placeholder="请输入账号" />
					<input class="input-field" v-model="mitaForm.password" type="password" placeholder="请输入密码" />
				</view>
				<view class="popup-btns">
					<button class="cancel-btn" @click="closeMitaPopup">取消</button>
					<button class="confirm-btn" @click="confirmMitaLogin">确认</button>
				</view>
			</view>
		</uni-popup>
	</view>
</template>
<script>
	import {
		sendUserPrompt,
		sendAgentPrompt,
		sendYBPrompt,
		sendMTPrompt
	} from '@/api/report'
	import storage from '@/utils/storage'
	import constant from '@/utils/constant'
	import WebSocketService from "@/utils/websocket.js";
	import {
		v4 as uuidv4
	} from 'uuid';
	export default {
		data() {
			return {
				isLoading: false, // 加载状态
				isHostOnline: false, // 默认在线
				isTencentLoggedIn: false,
				isMitaLoggedIn: false,
				tencentUsername: '',
				YBname: '',
				MTname: '',
				userId: '',
				corpId: '',
				username: '',
				mitaUsername: '',
				isExecuting: false,
				taskProgress: 0,
				scrollTop: 0,
				currentIndex:0,
				textIndex1:0,
				textIndex2:0,
				textIndex3:0,
				streamOutput: '',
				tencentConfig: {
					deepThinking: false,
					deepResearch: false,
					MtCsk: false
				},
				mitaOptions: [{
						label: '简洁',
						value: 'simple'
					},
					{
						label: '深入',
						value: 'deep'
					},
					{
						label: '研究',
						value: 'research'
					}
				],
				userInfoReq: {
					userPrompt: '',
					userId: '',
					corpId: '',
					taskId: '',
					roles: ''
				},
				mitaConfig: 'simple',
				resultContent: '',
				currentTab: 'AGENT',
				aiResults: [{
						type: 'AGENT',
						label: '福帮手智能体',
						content: ''
					},
					{
						type: 'YB',
						label: '腾讯元宝',
						content: ''
					},
					{
						type: 'MT',
						label: '秘塔AI',
						content: ''
					}
				],
				YbAnswer: '',
				AgentAnswer: '',
				MtAnswer: '',
				tencentForm: {
					account: '',
					code: ''
				},
				mitaForm: {
					account: '',
					password: ''
				},
				countdown: 0
			}
		},
		computed: {
			countDownText() {
				return this.countdown > 0 ? `${this.countdown}s` : '获取验证码'
			},
			isCountingDown() {
				return this.countdown > 0
			},
			canExecute() {
				return (this.isTencentLoggedIn || this.isMitaLoggedIn) && this.userInfoReq.userPrompt.trim() !== '' && this
					.taskProgress === 0
			}
		},

		onShow(options) {
			 if (options.scene === 1038) {
			        // 获取跳转回来的数据
			        const { finalCallbackData } = options.referrerInfo.extraData;
			        const { code, state } = JSON.parse(finalCallbackData);
			        console.log('授权成功，code:', code, 'state:', state);
			        // 在这里处理获取到的code和state
			    }
			this.userId = storage.get(constant.userId);
			this.corpId = storage.get(constant.corpId);

			this.userInfoReq.userId = storage.get(constant.userId);
			this.userInfoReq.corpId = storage.get(constant.corpId);
			this.username = storage.get(constant.username);

			if (this.userId != '') {
				if (this.taskProgress === 0 && this.isLoading == false) {
					this.isLoading = true

					this.initWebSocket();
				}

			} else {
				uni.navigateTo({
					url: '/pages/login/index'
				});
			}
		},
		onUnload() {
			// 页面卸载时关闭 WebSocket 连接
			this.closeWebSocket();
		},
		mounted() {

		},
		methods: {

			refreshStatus() {
				this.isLoading = true
				this.sendMessage("PLAY_CHECK_YB_LOGIN", this.userId, this.corpId, '', '', '', '');
				
			},
			initWebSocket() {
				const clientId = this.userId; // 可动态生成或从用户信息获取
				const url = `wss://yanghangxing.xiaomy.net/websocket?clientId=mini-${clientId}`;
				// const url = `wss://qkx.meooota.com/yfxServer/websocket?clientId=mini-${clientId}`;

				this.wsService = new WebSocketService(url);
				this.wsService.connect();
				// 设置消息回调函数
				this.wsService.setMessageCallback((message) => {
					this.handleMessage(message);
				});
			},

			// 处理接收到的消息
			handleMessage(message) {
				const data = JSON.stringify(message);
				console.log("回调消息", data)
				if ((data.includes("RETURN_YB_STATUS") || data.includes("LOGIN_YB")) && !data.includes("false") && message
					.status != '') {
					this.isHostOnline = true;
					this.isTencentLoggedIn = true;

					this.YBname = message.status;
					this.isLoading = false;
				}
				if ((data.includes("RETURN_MT_STATUS") || data.includes("LOGIN_MT")) && !data.includes("false") && message
					.status != '') {
					this.isHostOnline = true;
					this.isMitaLoggedIn = true;
					this.MTname = message.status;
					this.isLoading = false;
				}
				//如果输错密码，让他继续能登陆
				

				if (data.includes("online")) {
					this.isHostOnline = true;
					this.sendMessage("PLAY_CHECK_YB_LOGIN", this.userId, this.corpId, '', '', '', '');
					this.sendMessage("PLAY_CHECK_MT_LOGIN", this.userId, this.corpId, '', '', '', '');
				} else if (data.includes("offline")) {
					this.isHostOnline = false;
				} else {
					this.isHostOnline = true;
				}

			},
			// 发送消息
			sendMessage(type, userId, corpId, phone, code, username, password) {
				if (this.wsService) {
					this.wsService.sendMessage({
						type: type,
						userId: userId,
						corpId: corpId,
						username: username,
						password: password,
						phone: phone,
						code: code
					});
				} else {
					console.error('WebSocket 未连接');
				}
			},

			// 关闭 WebSocket 连接
			closeWebSocket() {
				if (this.wsService) {
					this.wsService.close();
					this.wsService = null;
				}
			},


			showTencentLogin() {
				this.$refs.tencentPopup.open()
			},
			showMitaLogin() {
				// this.$refs.mitaPopup.open()
				const clientId = "4cca97a84f4e468ea63824a105426a80";
				      const state = "空";
				
				      uni.navigateToMiniProgram({
				        appId: 'wxd45c635d754dbf59',
				        path: 'packages/miniapp-open/open-auth/open-auth?client_id=' + encodeURIComponent(clientId) + '&state=' + encodeURIComponent(state),
				        success: (res) => {
				          console.log('跳转成功', res);
				        },
				        fail: (err) => {
				          console.error('跳转失败', err);
				        }
				      });
			},
			closeTencentPopup() {
				this.$refs.tencentPopup.close()
				this.tencentForm = {
					account: '',
					code: ''
				}
			},
			closeMitaPopup() {
				this.$refs.mitaPopup.close()
				this.mitaForm = {
					account: '',
					password: ''
				}
			},
			sendVerifyCode() {
				if (!this.tencentForm.account) {
					uni.showToast({
						title: '请输入账号',
						icon: 'none'
					})
					return
				}

				this.sendMessage("PLAY_GET_VERIFY_CODE", this.userId, this.corpId, this.tencentForm.account, '', '', '');

				this.countdown = 60
				const timer = setInterval(() => {
					this.countdown--
					if (this.countdown <= 0) {
						clearInterval(timer)
					}
				}, 1000)
			},
			confirmTencentLogin() {
				if (!this.tencentForm.account || !this.tencentForm.code) {
					uni.showToast({
						title: '请填写完整信息',
						icon: 'none'
					})
					return
				}

				this.sendMessage("PLAY_LOGIN_YB", this.userId, this.corpId, this.tencentForm.account, this.tencentForm
					.code, '', '');
				this.isTencentLoggedIn = true
				this.closeTencentPopup()
				this.isLoading = true;
			},
			confirmMitaLogin() {
				if (!this.mitaForm.account || !this.mitaForm.password) {
					uni.showToast({
						title: '请填写完整信息',
						icon: 'none'
					})
					return
				}
				this.sendMessage("PLAY_LOGIN_MT", this.userId, this.corpId, '', '', this.mitaForm.account, this.mitaForm
					.password);

				this.isMitaLoggedIn = true
				this.closeMitaPopup()
				this.isLoading = true;

			},
			updateTencentConfig(key, value) {
				this.tencentConfig[key] = value
			},
			onMitaConfigChange(e) {
				this.mitaConfig = e.detail.value
			},
			copyResult() {
				if (!this.resultContent) return;

				// 使用正则表达式去除 HTML 标签
				const plainText = this.resultContent.replace(/<[^>]+>/g, '');

				// 设置剪贴板数据
				uni.setClipboardData({
					data: plainText,
					success: () => {
						uni.showToast({
							title: '复制成功',
							icon: 'success'
						});
					}
				});
			},
			executeTask() {
				if (!this.canExecute) return
				this.isExecuting = true
				this.taskProgress = 0
				this.AgentAnswer = ''
				this.YbAnswer = ''
				this.MtAnswer = ''
				this.resultContent = ''
				this.streamOutput = ''



				this.userInfoReq.roles = '';

				this.userInfoReq.taskId = uuidv4();
				if (this.tencentConfig.deepThinking) {
					this.userInfoReq.roles = this.userInfoReq.roles + 'yb-sdsk,';
				}
				if (this.tencentConfig.deepResearch) {
					this.userInfoReq.roles = this.userInfoReq.roles + 'yb-sdyj,yb-sdsk,';
				}
				if (this.tencentConfig.MtCsk) {
					this.userInfoReq.roles = this.userInfoReq.roles + 'mt-csk,';
				}
				
				if (this.mitaConfig == 'simple') {
					this.userInfoReq.roles = this.userInfoReq.roles + 'mt-jj,'
				}
				if (this.mitaConfig == 'deep') {
					this.userInfoReq.roles = this.userInfoReq.roles + 'mt-sr,'
				}
				if (this.mitaConfig == 'research') {
					this.userInfoReq.roles = this.userInfoReq.roles + 'mt-yj,'
				}
				console.log("参数：", this.userInfoReq)
				// sendUserPrompt(this.userInfoReq).then(res => {
				// 	this.taskProgress +=25
				// })
				this.taskProgress += 25;

				sendAgentPrompt(this.userInfoReq).then(res => {
					this.taskProgress += 25
					if (res.data != null) {
						this.AgentAnswer = "<br>" + res.data;
					} else {
						this.AgentAnswer = "<br><h2 style='font-size:12px;color:red'>当前访问人数过多，请稍后重试。</h2>";
					}

				})

				sendYBPrompt(this.userInfoReq).then(res => {
					this.taskProgress += 25
					if (res.data != null) {
						this.YbAnswer = " <br>" + res.data;
					} else {
						this.YbAnswer = "<br><h2 style='font-size:12px;color:red'>当前访问人数过多，请稍后重试。</h2>";
					}

				})

				sendMTPrompt(this.userInfoReq).then(res => {
					this.taskProgress += 25
					if (res.data != null) {
						this.MtAnswer = "<br>" + res.data;
					} else {
						this.MtAnswer = "<br><h2 style='font-size:12px;color:red'>当前访问人数过多，请稍后重试。</h2>";
					}

				})


			// 	const outputText1 =
			// 		'好的，我会先把几个 AI 工具都打开，包括福帮手智能体、腾讯元宝和秘塔AI，确保它们的对话页面都加载完成。\n\n接下来，根据用户勾选的配置，选择需要使用的 AI 以及对应的功能，确保每个 AI 都能处理相应的任务。\n\n然后，把用户输入的指令一个个输入到 AI 里，就像手动操作一样，把内容填进去，确保每个 AI 都能收到正确的指令。输入完成后，点击发送按钮，让它们开始处理。';
			
			// 	const outputText2 =
			// 		'\n\n现在，各个AI 正在分析指令，需要稍等一下，让它们思考并给出答案。请耐心等待结果哦';

			// 	 this.currentIndex = 0
			// 	 this.textIndex1 = outputText1.length
			// 	 this.textIndex2 = outputText2.length
			
				const simulateProgress = () => {
					if (this.taskProgress < 100) {

		// 				const streamInterval = setInterval(() => {
		// 					if (this.taskProgress == 25) {
							
		// 						if (this.currentIndex <  this.textIndex1) {
		// 							this.streamOutput += outputText1[this.currentIndex]
		// 							this.currentIndex++
		
		// 						} else {
		// 							this.currentIndex = 0;
		// 							this.textIndex1 = 0;
		// 						}
								
								
		// 					}
		// 					if (this.taskProgress == 50) {
								
		// 						if (this.currentIndex < this.textIndex2) {
		// 							this.streamOutput += outputText2[this.currentIndex]
		// 							this.currentIndex++
							
		// 						} else {
		// 							this.currentIndex = 0;
		// 							this.textIndex2 = 0;
		// 						}
								
		// 					}
		// 					if(this.taskProgress == 75){
		// 						clearInterval(streamInterval)
		// 					}
							
		// 				}, 500)
						setTimeout(simulateProgress, 500)
					} else {

						this.resultContent = this.AgentAnswer
						this.taskProgress = 0
						this.isExecuting = false
						uni.showToast({
							title: '执行完成',
							icon: 'success'
						})
					}
				}
				simulateProgress()

			},
	
			switchTab(tab) {
				this.currentTab = tab
				if (tab == 'AGENT') {
					this.resultContent = this.AgentAnswer
				} else if (tab == 'YB') {
					this.resultContent = this.YbAnswer
				} else if (tab == 'MT') {
					this.resultContent = this.MtAnswer
				}

			}
		}
	}
</script>
<style>
	page {
		height: 100%;
	}

	.host-status {
		display: flex;
		align-items: center;
		gap: 12rpx;
		background-color: #FFFFFF;
		padding: 16rpx 24rpx;
		border-radius: 16rpx;
		margin-bottom: 30rpx;
		box-shadow: 0 2px 12px rgba(0, 0, 0, 0.04);
	}

	.refresh-icon {
		margin-left: auto;
		width: 32px;
		height: 32px;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
	}

	.refresh-icon:active {
		opacity: 0.7;
	}

	.loading-dots {
		margin-left: 12rpx;
		margin-bottom: 60rpx;
		font-size: 16px;
		color: #666666;
		animation: dotAnimation 1.4s steps(4, end) infinite;
		position: relative;
	}

	.loading-dots::after {
		content: '';
		position: absolute;
		animation: dots 1.4s linear infinite;
	}

	@keyframes dots {
		0% {
			content: '.';
		}

		25% {
			content: '...';
		}

		50% {
			content: '....';
		}

		75% {
			content: '......';
		}
	}

	.host-status-dot {
		width: 12rpx;
		height: 12rpx;
		border-radius: 50%;
		background-color: #ff4d4f;
	}

	.host-status-dot.online {
		background-color: #1ec57c;
	}

	.host-status-text {
		font-size: 14px;
		color: #333333;
		font-weight: 500;
	}

	.container {
		height: 100%;
		padding: 30rpx;
		background-color: #F5F7FA;
	}

	.login-section {
		display: flex;
		gap: 20rpx;
		margin-bottom: 30rpx;
	}

	.login-card {
		flex: 1;
		background-color: #FFFFFF;
		border-radius: 16rpx;
		padding: 20rpx;
		box-shadow: 0 2px 12px rgba(0, 0, 0, 0.04);
	}

	.login-title {
		font-size: 14px;
		color: #333333;
		margin-bottom: 16rpx;
		font-weight: 500;
	}

	.login-btn {
		display: flex;
		align-items: center;
		gap: 8rpx;
		color: #666666;
		font-size: 14px;
	}

	.logged-status {
		display: flex;
		align-items: center;
		gap: 12rpx;
	}

	.username {
		font-size: 14px;
		color: #333333;
		font-weight: 500;
	}

	.status-text {
		font-size: 12px;
		color: #1ec57c;
		background-color: rgba(30, 197, 124, 0.1);
		padding: 4rpx 12rpx;
		border-radius: 20rpx;
	}

